px 0;\r\n}\r\n\r\n.vehicle-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n  padding: 12px 15px;\r\n  border-bottom: 1px solid #f1f3f5;\r\n  cursor: pointer;\r\n  transition: background-color 0.2s;\r\n}\r\n\r\n.vehicle-item:hover {\r\n  background-color: #f8f9fa;\r\n}\r\n\r\n.vehicle-item.selected {\r\n  background-color: #e9f5ff;\r\n  border-left: 4px solid #007bff;\r\n  padding-left: 11px;\r\n}\r\n\r\n.vehicle-item.inactive {\r\n  opacity: 0.6;\r\n}\r\n\r\n.vehicle-item-icon {\r\n  width: 36px;\r\n  height: 36px;\r\n  border-radius: 50%;\r\n  background-color: #3498db;\r\n  color: white;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  font-size: 16px;\r\n  flex-shrink: 0;\r\n  border: 2px solid white;\r\n  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.vehicle-item-details {\r\n  flex: 1;\r\n  min-width: 0;\r\n}\r\n\r\n.vehicle-item-name {\r\n  font-weight: 600;\r\n  margin-bottom: 2px;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n.vehicle-item-info {\r\n  font-size: 12px;\r\n  color: #6c757d;\r\n  margin-bottom: 2px;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n.vehicle-item-driver {\r\n  font-size: 12px;\r\n  color: #495057;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n.vehicle-item-status {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: flex-end;\r\n  min-width: 70px;\r\n}\r\n\r\n.status-badge {\r\n  padding: 2px 8px;\r\n  border-radius: 12px;\r\n  font-size: 11px;\r\n  font-weight: 500;\r\n  color: white;\r\n  margin-bottom: 4px;\r\n  white-space: nowrap;\r\n}\r\n\r\n.status-badge.moving {\r\n  background-color: #2ecc71;\r\n}\r\n\r\n.status-badge.stopped {\r\n  background-color: #e74c3c;\r\n}\r\n\r\n.status-badge.offline {\r\n  background-color: #6c757d;\r\n}\r\n\r\n.vehicle-speed {\r\n  font-size: 11px;\r\n  color: #6c757d;\r\n  margin-bottom: 2px;\r\n}\r\n\r\n.vehicle-last-update {\r\n  font-size: 10px;\r\n  color: #adb5bd;\r\n}\r\n\r\n.no-vehicles {\r\n  padding: 30px 20px;\r\n  text-align: center;\r\n  color: #6c757d;\r\n}\r\n\r\n.no-vehicles i {\r\n  font-size: 24px;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.loading-container {\r\n  padding: 20px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 10px;\r\n  font-size: 14px;\r\n  color: #6c757d;\r\n}\r\n\r\n/* Notifications section */\r\n.notifications-section {\r\n  border-top: 1px solid #e9ecef;\r\n  padding: 0;\r\n  max-height: 300px;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.notifications-header {\r\n  padding: 10px 15px;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  background-color: #f8f9fa;\r\n  border-bottom: 1px solid #e9ecef;\r\n}\r\n\r\n.notifications-header h4 {\r\n  margin: 0;\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 6px;\r\n}\r\n\r\n.notification-badge {\r\n  background-color: #dc3545;\r\n  color: white;\r\n  font-size: 10px;\r\n  padding: 2px 6px;\r\n  border-radius: 10px;\r\n  font-weight: 700;\r\n}\r\n\r\n.clear-notifications {\r\n  padding: 0;\r\n  font-size: 12px;\r\n  color: #6c757d;\r\n}\r\n\r\n.notifications-list {\r\n  overflow-y: auto;\r\n  max-height: 250px;\r\n  padding: 5px 0;\r\n}\r\n\r\n.notification-item {\r\n  display: flex;\r\n  padding: 8px 15px;\r\n  border-bottom: 1px solid #f1f3f5;\r\n  gap: 10px;\r\n}\r\n\r\n.notification-icon {\r\n  font-size: 16px;\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.notification-item.success .notification-icon {\r\n  color: #28a745;\r\n}\r\n\r\n.notification-item.error .notification-icon {\r\n  color: #dc3545;\r\n}\r\n\r\n.notification-item.warning .notification-icon {\r\n  color: #ffc107;\r\n}\r\n\r\n.notification-item.info .notification-icon {\r\n  color: #17a2b8;\r\n}\r\n\r\n.notification-content {\r\n  flex: 1;\r\n}\r\n\r\n.notification-message {\r\n  font-size: 12px;\r\n  margin-bottom: 3px;\r\n}\r\n\r\n.notification-time {\r\n  font-size: 10px;\r\n  color: #adb5bd;\r\n}\r\n\r\n.no-notifications {\r\n  padding: 20px;\r\n  text-align: center;\r\n  color: #6c757d;\r\n  font-size: 12px;\r\n}\r\n\r\n.no-notifications i {\r\n  font-size: 20px;\r\n  margin-bottom: 8px;\r\n}\r\n\r\n/* Map container - for Leaflet */\r\n.map-container {\r\n    flex: 1;\r\n    position: relative;\r\n    overflow: hidden;\r\n    height: 100%;\r\n    min-height: 500px !important;\r\n    width: 100%;\r\n    background-color: #f7f7f7;\r\n  }\r\n\r\n  .map-content {\r\n    position: absolute !important;\r\n    top: 0;\r\n    left: 0;\r\n    bottom: 0;\r\n    right: 0;\r\n    width: 100% !important;\r\n    height: 100% !important;\r\n    z-index: 1;\r\n  }\r\n\r\n  .leaflet-container {\r\n    height: 500px !important;\r\n    width: 100% !important;\r\n    z-index: 1;\r\n  }\r\n\r\n  \r\n.map-loading {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: rgba(255, 255, 255, 0.8);\r\n  z-index: 10;\r\n}\r\n\r\n.map-controls {\r\n  position: absolute;\r\n  top: 10px;\r\n  left: 10px;\r\n  z-index: 10;\r\n  background-color: rgba(255, 255, 255, 0.9);\r\n  border-radius: 4px;\r\n  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);\r\n  padding: 5px;\r\n}\r\n\r\n.control-group {\r\n  display: flex;\r\n  padding: 5px;\r\n}\r\n\r\n.map-style-select {\r\n  padding: 6px 8px;\r\n  border-radius: 4px;\r\n  border: 1px solid #ced4da;\r\n  font-size: 12px;\r\n  margin-right: 5px;\r\n}\r\n\r\n.control-button {\r\n  width: 30px;\r\n  height: 30px;\r\n  border-radius: 4px;\r\n  border: 1px solid #ced4da;\r\n  background-color: white;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  cursor: pointer;\r\n  margin-left: 3px;\r\n  transition: all 0.2s;\r\n}\r\n\r\n.control-button:hover {\r\n  background-color: #f8f9fa;\r\n}\r\n\r\n.control-button.active {\r\n  background-color: #007bff;\r\n  color: white;\r\n  border-color: #007bff;\r\n}\r\n\r\n/* Leaflet specific styles */\r\n.vehicle-marker {\r\n  background: transparent !important;\r\n  border: none !important;\r\n}\r\n\r\n/* Fix Leaflet marker icon issues */\r\n.leaflet-div-icon {\r\n  background: transparent;\r\n  border: none;\r\n}\r\n\r\n/* Responsive adjustments */\r\n@media (max-width: 992px) {\r\n  .tracking-layout {\r\n    flex-direction: column;\r\n  }\r\n  \r\n  .tracking-sidebar {\r\n    width: 100%;\r\n    height: 300px;\r\n    border-right: none;\r\n    border-bottom: 1px solid #e9ecef;\r\n  }\r\n  \r\n  .map-container {\r\n    flex: 1;\r\n    height: calc(100vh - 360px); /* Adjust for sidebar height */\r\n  }\r\n}"],"sourceRoot":""}]);
